@import 'reset';


* {
  box-sizing: border-box;
}

html, body {
  height: 100%;
  font-family: sans-serif;
}

html {
  font-size: calc( 12px + (24 - 12) * ( (100vw - 400px) / ( 800 - 400) ));
}

%content-wrap {
  font-size: 60%;
  line-height: 0.8rem;
  color: lighten(black, 30);
}

%article-wrap {
  padding: 0.5rem 0;
}

%article-title {
  padding: 0.5rem;
  font-weight: bold;
}

%article-item {
  text-align: justify;
  text-indent: 1rem;
}
